import { Component, OnInit } from '@angular/core';
import { ApiService } from '../_services/api.service';
import { AppConfig } from '../app.config';
import { AuthenticationService } from '../_services/authentication.service';
import { Router } from '../../../node_modules/@angular/router';

@Component({
  selector: 'app-badNews',
  templateUrl: './badNews.component.html',
  styleUrls: ['./badNews.component.css']
})
export class BadNewsComponent implements OnInit {

  news: any[];
  userActive:boolean;
  constructor(private newsService: ApiService,private urlNews:AppConfig,private authenticate:AuthenticationService,private router:Router) {}

  ngOnInit() {
    this.newsService.getData(this.urlNews.apiBadNewsEndpoint).subscribe(data=>this.news=data);
  }

}
