import { Component, OnInit, Input } from '@angular/core';
import { AppConfig } from '../app.config';
import { ApiService } from '../_services/api.service';
import { CurrentUser } from '../_models/currentUser';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';


@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.css']
})
export class CommentsComponent implements OnInit {
@Input() idPost:string;
currentUser: CurrentUser;
comments:any[];
state:boolean = true;
commentButtonText:string = 'Смотреть комментарии';
  constructor(private newsService: ApiService,private urlComments:AppConfig,private hhtp: HttpClient) { }

  ngOnInit() {
  }
  getCommentsByIDPost(idPost:string){
    this.newsService.getData(this.urlComments.apiCommentsEndpoint+idPost).subscribe(data=>this.comments=data);
    this.state = this.state == false ? true: false;
    this.commentButtonText= this.commentButtonText=='Скрыть комментарии' ?'Смотреть комментарии':'Скрыть комментарии';
  }

  deleteComment(id:string,idPost:string)
  {
    if(window.confirm('Are sure you want to delete this comment?')){
    this.hhtp.delete(this.urlComments.apiDeleteCommentEndpoint+id)
    .subscribe(
      response => {
       // alert("Comment delete successfully");
        this.newsService.getData(this.urlComments.apiCommentsEndpoint+idPost).subscribe(data=>this.comments=data);
      },
      (err: HttpErrorResponse) => {
        alert(err.error['Message']);
      });
    }
  }

  isVisibleButton()
  {
    this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
    if(this.currentUser && this.currentUser.Role=='Admin') return false;
    return true;
  }
}
